<template>
    <div style="display: flex;">
      <v-sheet color="#eee" class="pos-sticky">
        <v-list color="#eee" class="pos-sticky">
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="home"
                :size="36"
                class="v-hover v-grey"
                @click="setType('Home')"
              >
                <v-icon>mdi-home</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="calendar"
                :size="36"
                class="v-hover"
                @click="setType('Calendar')"
              >
                <v-icon>mdi-calendar</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="chat"
                :size="36"
                class="v-hover"
                @click="setType('Chat')"
              >
                <v-icon>mdi-chat-outline</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="hr"
                :size="36"
                class="v-hover"
                @click="setType('HR')"
              >
                <v-icon>mdi-account-tie</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="ceo"
                :size="36"
                class="v-hover"
                @click="setType('CEO')"
              >
                <v-icon>mdi-account-star</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="settings"
                :size="36"
                class="v-hover"
                @click="setType('Settings')"
              >
                <v-icon>mdi-cog</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
        </v-list>
      </v-sheet>
      <v-container fluid class="fix-container">
        <v-row v-if="!onlyMiddle">
          <v-col
            cols="2"
          >
          <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              color="bg-color"
            >
            <v-row>
                <v-col cols="3">
                    <v-avatar
                      class="mt-2"
                      color="primary"
                      size="40"
                      tile
                    >
                      <span class="initials" v-if="!user.photo">{{initials}}</span>
                      <img :src="user.photo" v-if="user.photo">
                    </v-avatar>
                </v-col>
                <v-col class="mt-2">
                    <h3>{{user.firstName}} {{user.lastName}}</h3>
                    <p><a href="#" class="edit-link" @click="viewProfile()">View Profile</a></p>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-list
              dense
              class="w-menu"
              v-model="selectedItem"
            >
              <v-list-item-group mandatory>
                  <v-list-item 
                      v-for="(item, i) in items"
                      :key="i"
                      @click="goToPage(item)"
                  >
                    <v-list-item-content>
                      <v-list-item-title>{{item}}</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
              </v-list-item-group>
            </v-list>
            </v-sheet>
          </div>
          </v-col>

          <v-col
            cols="7"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>

          <v-col
            cols="3"
          >
            <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
               <v-card class="w-secondary" v-if="lastAnnouncement">
                 <v-card-title class="head">
                  <span> <v-icon>mdi-bullhorn</v-icon> Announcement </span>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    {{lastAnnouncement.NAME}}
                  </v-card-text>
                      <v-card-actions>
                  <v-btn text @click="announcementDialog = true">Open</v-btn>
                  <v-dialog
                     v-model="announcementDialog"
                     fullscreen
                     hide-overlay
                     transition="dialog-bottom-transition"
                   >
                     <template v-slot:activator="{ on, attrs }">
                         <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
                     </template>
                      <v-card>
                         <v-toolbar
                           dark
                           color="primary"
                         >
                           <v-btn
                             icon
                             dark
                             @click="announcementDialog = false"
                           >
                             <v-icon>mdi-close</v-icon>
                           </v-btn>
                           <v-toolbar-title>Announcement</v-toolbar-title>
                         </v-toolbar>  
                         <v-card-text><div v-html="announcementContent"></div></v-card-text>                    
                       </v-card>
                   </v-dialog>
                </v-card-actions>
              </v-card>
              <v-card class="mt-2 w-secondary" v-if="lastCeoMessage">
                <v-card-title class="head"><v-icon>mdi-account-star</v-icon><span class="name">CEO</span></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                   {{lastCeoMessage.NAME}}
                </v-card-text>
                <v-card-actions>
                  <v-btn text @click="ceoDialog = true">Open</v-btn>
                  <v-dialog
                     v-model="ceoDialog"
                     fullscreen
                     hide-overlay
                     transition="dialog-bottom-transition"
                   >
                     <template v-slot:activator="{ on, attrs }">
                         <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
                     </template>
                         <v-toolbar
                           class="title"
                         >
                           <v-btn
                             icon
                             @click="ceoDialog = false"
                           >
                             <v-icon>mdi-close</v-icon>
                           </v-btn>
                           <v-toolbar-title>CEO Message</v-toolbar-title>
                         </v-toolbar> 
                         <v-card color="#f7f8f8">
                         <v-card-text class="ceo-message">
                           <v-container fluid>
                            <div v-html="ceoContent"></div>
                           </v-container>
                          </v-card-text>  
                         </v-card>  
                   </v-dialog>
                </v-card-actions>
              </v-card>
              <v-card class="mt-2 w-secondary">
                <v-card-title class="head"><v-icon>mdi-poll</v-icon> <span class="name">POLL</span></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <vue-poll v-bind="pollQuestions" @addvote="addVote"/>
                </v-card-text>
              </v-card>
            </v-sheet>
            </div>
          </v-col>
        </v-row>
        <v-row v-if="onlyMiddle">
          <v-col cols="1"></v-col>
          <v-col cols="10">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>
      </v-container>
    </div>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 import VuePoll from 'vue-poll';
 export default {
   name: 'LayoutDefault',
   components: { VuePoll },
   data: () => ({ 
     items: window.opentext.leftMenuNames,
     selectedItem: 0,
     isClicked: false,
     onlyMiddle: false,
     type: 'Home',
     lastCeoMessage: null ,
     lastAnnouncement: null,
     ceoContent: "",
     announcementContent: "",
     ceoDialog: false,
     announcementDialog: false,
     pollQuestions: {
        question: 'How long have you been using our product/service?',
        answers: [
            { value: 1, text: 'Less than a month', votes: 3 },
            { value: 2, text: '1 to 6 months', votes: 2 },
            { value: 3, text: '6 months to 1 year', votes: 1 }
        ]
     }
   }),
   watch: {
     type: function(val){
       switch (val) {
         case 'Home':
           this.onlyMiddle = false;
           this.goTo('/newsFeed');
           break;
         case 'Chat':
           this.onlyMiddle = true;
           this.goTo('chat');
           break;
          case 'Settings':
           this.onlyMiddle = true;
           this.goTo('admin');
            break;
          case 'Calendar':
            this.onlyMiddle = true;
            this.goTo('calendar');
            break;
          case 'CEO':
            this.onlyMiddle = true;
            this.goTo('ceo');
            break;
          case 'HR':
            this.onlyMiddle = true;
            this.goTo('hr');
            break;
         default:
           break;
       }
     },
     ceoMessages: function(val){
       this.lastCeoMessage = val.find(x=>{
                console.log(new Date(x.DATEEFFECTIVE).getTime());
                var effective_date = new Date(x.DATEEFFECTIVE).getTime();
                var expiration_date = new Date(x.DATEEXPIRATION).getTime();
                var current_date = new Date().getTime();
                if(current_date > effective_date && current_date < expiration_date){
                    return true;
                }
        });
        this.ceoContent = JSON.parse(this.lastCeoMessage.EXTENDEDDATA).Story;
     },
     announcements: function(val){
       console.log(val);
       this.lastAnnouncement = val.find(x=>{
                console.log(new Date(x.DATEEFFECTIVE).getTime());
                var effective_date = new Date(x.DATEEFFECTIVE).getTime();
                var expiration_date = new Date(x.DATEEXPIRATION).getTime();
                var current_date = new Date().getTime();
                if(current_date > effective_date && current_date < expiration_date){
                    return true;
                }
        });
        if(this.lastAnnouncement)
          this.announcementContent = JSON.parse(this.lastAnnouncement.EXTENDEDDATA).Story;
     }
   },
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ]),
     ...mapGetters('admin', ['ceoMessages', 'announcements'])
   },
   methods: {
     ...mapActions(['initialLoad']),
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhoto'
     ]),
     ...mapActions('admin', [
       'getPollData'
     ]),
    ...mapActions('hr', ['getVacancyHealines']),
     setType: function(t){
      this.type = t;
      for (const el of document.getElementsByClassName('v-hover')) {
          el.classList.remove('v-grey');
      }
      var idName = t.toLowerCase();
      document.getElementById(idName).classList.add('v-grey');
     }, 
     viewProfile(){
       this.goTo('profile');
     },
     goToPage(item){
       switch (item) {
          case 'News Feed':
           this.goTo('newsFeed');
           break;
          case 'Innovations':
            this.goTo('innovations');
            break;
          case 'HR News':
            this.goTo('hrNews');
            break;
          case 'New Vacancies':
            this.goTo('hrVacancies');
            break;
          case 'Motivation Stories':
            this.goTo('hrMotivationStories');
            break;
          case 'Instructions':
           this.goTo('instructions');
           break;
          case 'HR':
            this.goTo('hr');
            break;
          case 'Report':
            this.goTo('report');
            break;
          case 'CEO':
            this.goTo('ceo');
            break;
         default:
           this.goTo('/')
           break;
       }
     },
     goTo(routeUrl){
         this.$router.push(routeUrl);
     },
     addVote(){

     }
   },
   created: async function(){
     await this.getMemberData();
     await this.getMemberPhoto();
     //await this.getPollData();
     await this.initialLoad();
     this.goTo('/newsFeed');
   },
 }
</script>

<style lang="scss">
    .name{
      margin-left: 10px;
    }
    .title{
      background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
    }
    .head{
      background-color: #eee;
    }
    .fix-container{
      margin-left: 10px;
      margin-right: 10px;
    }
    .bg-color{
        background-color: #f7f8f8 !important;
    }
    .initials {
        color: white
    }
    .edit-link{
        text-decoration: unset;
    }
    .pos-sticky{
      position: sticky;
      top: 10px;
      height: 100vh;
    }
    .w-menu{
      background-color: unset !important;;
    }
    #v-nav{
      position: sticky;
    }
    .v-hover:hover{
      background-color: grey;
      cursor: pointer;
    }
    .v-grey{
      background-color: grey !important;;
    }
    .w-secondary{
      background-color: #f7f8f8!important;
    }
    .ceo-message{
      color: black !important;
    }
</style>