<template>
    <v-sheet
      rounded="lg"
      class="w-primary"
      min-height="85vh"
    >
      <v-container>
        <v-row>
          <v-col>
            <v-card
              class="w-secondary"
              min-height="85vh"
            >
            <v-card-title class="w-text w-primary">
                <v-icon>mdi-star-circle</v-icon> <span class="name">Motivation Story</span>
            </v-card-title>
              <v-virtual-scroll
                :items="motivationStories"
                max-height="85vh"
                item-height="50"
              >
                <template v-slot:default="{ item }">
                  <v-list-item @click="openBlogDialog(item.DATAID)" :key="item.GUID">     
                    <v-list-item-content>
                      <v-list-item-title>
                       <strong>{{ item.NAME }}</strong>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider></v-divider>
                </template>
              </v-virtual-scroll>
            </v-card>
          </v-col>
        </v-row>
      </v-container> 
       <v-dialog
         v-model="dialog"
         fullscreen
         hide-overlay
         transition="dialog-bottom-transition"
       >
         <template v-slot:activator="{ on, attrs }">
             <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
         </template>
          <v-card>
             <v-toolbar
               class="title"
             >
               <v-btn
                 icon
                 @click="dialog = false"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>HR Motivation Story</v-toolbar-title>
             </v-toolbar>  
             <v-card-text class="w-text">
               <v-container>
                  <div v-html="content"></div>
               </v-container>
              </v-card-text>                    
           </v-card>
       </v-dialog>  
    </v-sheet>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  name: 'HRMotivationStories',
  data: () => ({ 
    dialog: false,
    content: ''
  }),
  computed: {
    ...mapGetters('hr', [
          'motivationStories'
    ])
  },
  methods: {
    openBlogDialog: async function(id){
      const motivationStories = this.motivationStories.find(x=>x.DATAID === id);
      const data = JSON.parse(motivationStories.EXTENDEDDATA)
      this.content = data.Story;
      const imageId = data.ImageID;
      if(imageId){
         const config = {
           headers: { 'OTCSTicket': window.opentext.OTCSTicket }
         }
         let url = `${window.opentext.mainurl}/api/v1/nodes/${imageId}/content`;
         const response = await fetch(url, config);
         const image = await response.blob();
         this.content += `<img src="${window.URL.createObjectURL(image)}">`;
      }
      this.dialog = true;
    }
  },
  created: function() {
  }
}
</script>

<style lang="scss" scoped>
  .name{
    margin-left: 10px;
  }
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
   .w-text{
    color: black !important;
  }
</style>
